}

.payment {
  display: grid;
  width: 300px;
  height: 44vh;
}

.payment button {
  background-color: rgb(0, 175, 0);
}

.loading {
  height: 100px;
  width: 100%;
  background-image: linear-gradient(
    to right,
    rgb(227, 227, 227) 0%,
    rgb(180, 178, 178) 25%,
    rgb(85, 84, 84) 50%,
    rgb(39, 39, 39) 100%
  );
  background-size: 200% 100%;
  animation: ani 1s linear infinite;
}

@keyframes ani {
  0% {
    background-position: 10%;
  }

  50% {
    background-position: 150%;
  }

  100% {
    background-position: 450%;
  }
}

