.policy {
  background-color: var(--color-white);
  padding: 30px;
}

/* SinglePage */

.description {
  background-color: var(--color-white);
}

.review {
  background-color: var(--color-white);
}

.small-images img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border: 1px solid black;
}

.img-zoom {
  z-index: 1000;
}

/* Cart */
.cart-header {
  border-bottom: 1px solid #eaeaea;
}

.cart-col-1 {
  width: 40%;
}

.cart-col-2 {
  width: 25%;
}

.cart-col-3 {
  width: 25%;
}

/* checkout */

.three {
  width: 32%;
}

.two {
  width: 49%;
}

@media screen and (max-width: 650px) {
  .email-signup {
    display: grid;
  }

  .body-banner {
    width: 100%;
    height: 160px;
    object-fit: cover;
  }

  .personal-images {
    width: 80px;
    height: 80px;
  }

  .header-items a {
    padding: 10px 5px;
  }

  .single-product-login-and-all-the-information {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .single-product-images {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .single-product-images-others-div {
    display: flex;
  }

  .home-images {
    display: flex;
    flex-direction: column;
  }

  .main-banner {
    width: 100%;
  }

  .four-items {
    width: 100%;
  }

  .header {
    overflow: scroll;
  }

  .services {
    display: grid;
  }

  .small-banner-content {
    left: 12%;
  }

  .categories {
    width: 1000px;
  }

  .product-card-body {
    justify-content: center;
  }
}
